@import url(https://fonts.googleapis.com/css?family=Archivo+Black);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500);
* {
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
  font-weight: 400;
  margin: 0; }

.open-page {
  background-image: url(/b-travel/static/media/bg-map.2b1e7ebb.jpg);
  background-color: black;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  background-color: #3c3c3c;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .open-page-nav {
    position: absolute;
    top: 0;
    width: 100%;
    height: 50px;
    background-color: black; }
    .open-page-nav img {
      margin: 10px; }
  .open-page-image {
    width: 150px; }
  .open-page-title {
    max-width: 700px;
    width: 80%;
    text-align: center;
    color: white;
    font-size: 2.5rem; }
  .open-page-signup {
    margin-top: 25px;
    max-width: 700px;
    width: 80%; }
    .open-page-signup-text {
      width: 70%;
      height: 35px;
      padding-left: 10px; }
    .open-page-signup-submit {
      background-color: #e56e4a;
      color: white;
      height: 35px;
      width: 25%;
      float: right;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 0.8rem;
      cursor: pointer; }
  .open-page .sign-up-success {
    position: absolute;
    color: white;
    bottom: 30%; }

.feature-list .feature {
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .feature-list .feature.is-odd {
    flex-direction: row-reverse; }
  .feature-list .feature-black-background {
    width: 50vw;
    padding-top: 45%;
    position: relative;
    background: black; }
    .feature-list .feature-black-background .feature-background-image,
    .feature-list .feature-black-background .feature-image {
      position: absolute;
      margin-left: 10%; }
      .feature-list .feature-black-background .feature-background-image.image-is-odd,
      .feature-list .feature-black-background .feature-image.image-is-odd {
        right: 10%; }
    .feature-list .feature-black-background .feature-background-image {
      width: 55vw;
      top: 10%; }
    .feature-list .feature-black-background .feature-image {
      height: 60%;
      top: 20%; }
  .feature-list .feature-caption-container {
    position: absolute;
    width: 30%;
    right: 5%; }
    .feature-list .feature-caption-container.caption-is-odd {
      left: 5%; }
    .feature-list .feature-caption-container .feature-caption-title {
      font-size: 27px;
      font-weight: bold;
      margin-bottom: 10px; }
    .feature-list .feature-caption-container .feature-caption-body {
      font-size: 13.5px; }

@media only screen and (max-width: 900px) {
  .feature-list .feature-caption-container .feature-caption-title {
    font-size: 3vw; }
  .feature-list .feature-caption-container .feature-caption-body {
    font-size: 1.5vw; } }

